<template>
  <div class="row mt-0 d-flex align-items-start hide-scrollbar" id="login-page">
    <div class="col-lg-4 mx-auto px-0 flex-center">
      <div class="justify-content-center login-box box flex-column">
        <h2 class="mb-3">Welcome to Yiu's Villa ðŸ‘‹</h2>
        <p class="mb-4 normal-text gray">
          Please sign-in to your account and start the adventure
        </p>
        <form @submit.prevent="onSubmit" class="w-100">
          <label for="Email" class="custom-label">Email</label>
          <el-input
            name="Email"
            placeholder="Enter your email"
            class="w-100 mb-3"
            v-model="email"
          />

          <label for="Password" class="custom-label">Password</label>
          <el-input
            name="Password"
            class="w-100"
            type="password"
            placeholder="Enter your password"
            v-model="password"
          />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 my-3"
            style="width: 100% !important"
            type="primary"
            native-type="submit"
            >Login</el-button
          >
        </form>
        <!-- <div class="flex-center w-100 my-2">
          <a
            class="primary hover-underline pointer md-text"
            href="https://m.me/106339588511793"
            target="_blank"
            style="text-decoration: none"
            >Create new account? Contact our admin</a
          >
        </div> -->

        <div class="flex-center w-100">
          <a
            class="primary hover-underline pointer md-text"
            @click="
              $router.push({
                name: 'Forgot Password',
                query: { email: email },
              })
            "
            >Forgot password?</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
    };
  },

  created() {},
  computed: {
    ...mapState(["isDark"]),
  },
  methods: {
    onSubmit() {
      this.isLoading = true;
      this.$store
        .dispatch("logIn", {
          email: this.email,
          password: this.password,
        })
        .then((err) => {
          this.isLoading = false;
          if (err) {
            localStorage.setItem("isLogined", false);
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          }
        });
    },
  },
};
</script>
